body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
  overflow: auto;
  display: grid;
  grid-template-rows: auto 3em;
  grid-template-columns: auto;
  grid-template-areas: 
    "root"
    "footer";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: blue;
  text-decoration: none;
}

a:hover {
  color: skyblue;
}

.caution {
  grid-area: footer;
  width: 100%;
  background-repeat: repeat;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 700;
}

.caution p {
  margin: 0;
  padding: 2px;
  background-color: black;
  color: white;
}

.caution a,
.caution a:visited {
  color: skyblue;
}

.caution a:active,
.caution a:focus,
.caution a:hover {
  color: blue;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root {
  padding: 0 1em 0 1em;
  overflow:auto;
  grid-area: root;
}

h1 {
  font-size: 1.5em;
  font-weight: 400;
  text-align: center;
}

table {
  margin: 2em auto 0 auto;
}

td,
th {
  padding: 0.25em 1em;
}

th {
  text-align: left;
}

caption {
  font-size: 1.125em;
  font-weight: 700;
}

.table__number {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  text-align: right;
}

.center {
  text-align: center;
}

.tabbar {
  background-color: #fff;
  width: 100%;
}

.tabbar ul {
  list-style: none;
}

.tabbar li {
  display: inline-block;
}

.tab {
  background-color: transparent;
  color: green;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
}

.tab:hover {
  color: lightgreen;
}

.tab:disabled {
  color: #aaa;
  cursor: default;
}

.new_round {
  color: white;
  background-color: green;
  border-radius: 0.5em;
}

.new_round:hover {
  color: lightgray;
}

.inactive .table__player,
.inactive .table__number {
  opacity: 0.5;
}

.dummy {
  font-style: italic;
}

.table__roster td {
  vertical-align: top;
}
